import SolverService from './SolverService'
import ProductiveNonTermsSolver from './ProductiveNonTermsSolver'
import SetUtils from "../utils/SetUtils";

const TEST_CASES = [
    [
        'S-aBA',
        'S-YBXA',
        'B-XAX',
        'B-abC',
        'X-abX',
        'X-',
        'A-ASbX',
        'A-',
        'C-XABYaX',
        'C-YAab',
        'Y-BCaXB',
        'Y-XAC',
    ], [
        'S-YBa',
        'S-BXA',
        'A-ASbX',
        'A-',
        'B-XAAX',
        'B-abC',
        'C-ABXY',
        'C-abXAY',
        'X-Xaa',
        'X-',
        'Y-BAXBaY',
        'Y-abXA'
    ]
];

class ProductiveTransitionsSolver extends SolverService {

    constructor() {
        super('sp', 'prod-transitions', 'побудувати множину продуктивних переходів');
    }


    getTestCase(index) {
        let rules = [];
        for (let rule of TEST_CASES[index]) {
            let parts = rule.split('-');
            rules.push({
                left: parts[0],
                right: parts[1],
            })
        }

        return rules;
    }

    solve(input, stepOutputHandle, answerOutputHandle) {
        stepOutputHandle('Знайдемо множину продуктивних нетерміналів:');
        let prodNetermsVector = ProductiveNonTermsSolver.solve(input, stepOutputHandle, stepOutputHandle);          //vector <string>

        stepOutputHandle('Проаналізуємо, чи всі нетермінали в переходах є продуктивними(непродуктивні підкреслені):');
        let prodNeters = [];                                 //set <string>;

        for (let it of prodNetermsVector) SetUtils.insert(prodNeters, it);

        let output = '';
        for (let it of input) {
            let isOk = 1;

            let ruleOutput = it.replace('-', ' ➔ ');
            for (let i = 0; i < it.length; i++) {
                let c = it.charAt(i);
                if ('A' <= c && c <= 'Z' && !prodNeters.includes(c)) {
                    isOk = 0;
                    ruleOutput = ruleOutput.replace('/g/' + c, '<u>' + c + '</u>');
                }
            }

            if (isOk === 1) {
                stepOutputHandle(ruleOutput + ' &nbsp; продуктивний перехід');
                output += it.replace('-', ' ➔ ') + ' &nbsp; продуктивний перехід' + '<br>';
            }
            else {
                stepOutputHandle(ruleOutput + ' &nbsp; не продуктивний перехід');
                output += it.replace('-', ' ➔ ') + ' &nbsp; не продуктивний перехід' + '<br>';
            }
        }

        answerOutputHandle(output);
        return prodNetermsVector;
    }
}

let instance = new ProductiveTransitionsSolver();

export default instance;