<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div class="menu-wrapper" :class="{'menu-mobile' : showMobile}">
        <div class="menu-title">Розділи</div>
        <div class="menu-topics">
            <div v-for="(topic, index) in topics" :key="index" class="menu-topic">
                <div class="menu-topic-title" v-html="topic.title"></div>
                <div class="menu-topic-items">
                    <div v-for="(item, index) in topic.items" :key="index" v-html="item.title"
                         class="menu-topic-item " :class="{selected : isCurrItem(topic.href, item.href)}" v-on:click="onItemClick(topic.href, item.href)">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                selected: document.location.pathname,
            }
        },
        props: ['topics', 'showMobile', 'onItemSelected'],
        computed: {},
        components: {},
        methods: {
            isCurrItem(topic, theme) {
                return (topic + theme) === this.selected;
            },
            onItemClick(topic, theme) {
                this.selected = topic + theme;
                this.$router.push(topic + theme);

                this.onItemSelected();
            }
        },
        created(){
        },
        mounted(){
        }
    }
</script>
<style scoped>
    .menu-wrapper {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 9990;
        width: 300px;
        height: 100%;
        background: var(--content);
        overflow-y: auto;
    }

    .menu-title {
        border-bottom: 2px solid var(--lighter);

        text-align: center;
        color: var(--font-color-3);
        font-size: 32px;
        font-weight: bold;
        line-height: 59px;
    }

    .menu-topics {
        height: fit-content;
        padding-bottom: 60px;
    }

    .menu-topic {
        line-height: 24px;
    }

    .menu-topic-title {
        padding: 20px 0px 5px 20px;

        color: var(--font-color-1);
        font-size: 20px;
        font-weight: bold;
    }

    .menu-topic-items {
    }

    .menu-topic-item {
        padding: 8px 0px 8px 20px;


        font-size: 17px;
        line-height: 20px;
        color: var(--font-color-4);
    }

    .menu-topic-item:hover, .selected{
        background: var(--lighter);
        cursor: pointer;

        color: var(--font-color-1);
    }

    @media screen and (max-width: 1024px) {
        .menu-wrapper {
            display: none;
        }

        .menu-mobile {
            display: block;
            top: 60px;
            width: 100%;
        }
    }
</style>