import SolverService from './SolverService'
import ArrayUtils from '../utils/ArraysUtils'
import SetUtils from "../utils/SetUtils";

const TEST_CASES = [
    [
        'S-aBA',
        'S-YBXA',
        'B-XAX',
        'B-abC',
        'X-abX',
        'X-',
        'A-ASbX',
        'A-',
        'C-XABYaX',
        'C-YAab',
        'Y-BCaXB',
        'Y-XAC',
    ], [
        'S-YBa',
        'S-BXA',
        'A-ASbX',
        'A-',
        'B-XAAX',
        'B-abC',
        'C-ABXY',
        'C-abXAY',
        'X-Xaa',
        'X-',
        'Y-BAXBaY',
        'Y-abXA'
    ]
];

class ProductiveNonTermsSolver extends SolverService {

    constructor() {
        super('sp', 'prod-non-terms', 'побудувати множину продуктивних нетерміналів');
    }


    getTestCase(index) {
        let rules = [];
        for (let rule of TEST_CASES[index]) {
            let parts = rule.split('-');
            rules.push({
                left: parts[0],
                right: parts[1],
            })
        }

        return rules;
    }

    solve(input, stepOutputHandle, answerOutputHandle) {
        let dat = [];                                            //vector <pair<string, string>>
        let solution = [];                                       //vector <vector<string>>

        for (let it of input) {
            let ruleParts = it.split('-');
            dat.push({
                first: ruleParts[0],
                second: ruleParts[1],
            });
        }

        let resSet = [];                                        //set <string>

        for (;;) {
            let resSetNext = [];                                //set <string>

            for (let trans of dat) {
                let isOk = 1;

                for (let i = 0; i < trans.second.length; i++) {
                    let c = trans.second.charAt(i);
                    if ('A' <= c && c <= 'Z' && !resSet.includes(c)) isOk = 0;
                }

                if (isOk === 1) SetUtils.insert(resSetNext, trans.first);
            }

            let resNext = [];                                     //vector <string>

            for (let it of resSetNext) resNext.push(it);

            if (solution.length !== 0 && SetUtils.compare(solution[solution.length - 1], resNext)) {
                solution.push(resNext);

                break;
            }

            solution.push(resNext);
            resSet = resSetNext;
        }

        for (let i = 0; i < solution.length; i++) {
            let it = solution[i].sort();

            let output = 'S<t class="lwc">' + i + '</t> = ';
            output += ArrayUtils.printSet(it);

            if(i !== solution.length - 1) stepOutputHandle(output);
        }

        answerOutputHandle(ArrayUtils.printSet(solution[solution.length - 1]) + ' - множина продуктивних нетерміналів.');
        return solution[solution.length - 1];
    }
}

let instance = new ProductiveNonTermsSolver();

export default instance;