class ArrayUtils {

    static printSet(set, without) {
        if (set.length > 1) set.sort();
        for(let i = 0; i < set.length; i++) {
            if(set[i] === '') {
                set.splice(i, 1);
                i--;
            }
        }

        if(without) return set.join(', ');

        return '{ ' + set.join(', ') + ' }';
    }

}

export default ArrayUtils;