class SetUtils {

    static insert(set, item) {
        if(!set.includes(item)) set.push(item);
    }


    static compare(as, bs) {
        if(as.size !== undefined && bs.size !== undefined) {
            if(as.size !== bs.size) return false;
        }
        else {
            if(as.length !== bs.length) return false;
        }

        for(let i = 0; i < as.length; i++) {
            if(bs.indexOf(as[i]) === -1 ) return false;
        }

        return true;
    }

}

export default SetUtils;